.card-set__front, .card-set__back {
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  transition: transform 2.5s cubic-bezier(.4, 0, .2, 1);
}

.card-set__front {
  transform: perspective(600px)rotateY(0deg);
}

.card-set__front.is-flipped {
  transform: perspective(600px)rotateY(-180deg);
}

.card-set__back {
  position: absolute;
  transform: perspective(600px)rotateY(180deg);
}

.card-set__back.is-flipped, .card-set__back.is-flipped2, .card-set__back.is-flipped3 {
  transform: perspective(600px)rotateY(0deg);
}

.card {
  transform-style: preserve-3d;
  cursor: pointer;
  transition: transform 1s;
  position: relative;
}

.card__face {
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  position: absolute;
}

.card__face--back, .card.is-flipped {
  transform: rotateY(180deg);
}

.card-set__btn--front:hover .card-set__front {
  transform: perspective(600px)rotateY(-180deg);
}

